.tup {
  width: 90px;
  height: 90px;
}
body {
  font-size: 24px;
  line-height: 36px;
}
.mt-1 {
  margin-top: 8px;
}
.mb-1 {
  margin-bottom: 8px;
}
.mb-2 {
  margin-bottom: 16px;
}
.pt-3 {
  padding-top: 24px;
}
.mb-3 {
  margin-bottom: 24px;
}
.mb-4 {
  margin-bottom: 32px;
}
.mb-5 {
  margin-bottom: 40px;
}
.mb-6 {
  margin-bottom: 48px;
}
.mb-9 {
  margin-bottom: 72px;
}
.title {
  font-size: 80px;
  line-height: 94px;
  padding-bottom: 16px;
  border-bottom: 2px solid #000;
}
.container {
  width: 1200px;
  margin: 0 auto;
  
  position: relative;
  top: 200px;
}
.subTitle {
  font-size: 32px;
  line-height: 45px;
  border-bottom: 1px solid #000;
  padding-bottom: 8px;
}
.subTitle span {
  color: #a00404;
  padding-left: 16px;
  font-size: 24px;
}
.subTitle-white {
  text-align: center;
  border-bottom: 1px solid #000;
}
.subTitle-white span {
  color: #fff;
}
.darkBlue {
  background: #415165;
  color: #fff;
}
.skills {
  width: 996;
  margin: 0 auto;
  padding-bottom: 16px;
}
.skills-content {
  width: 230px;
  margin: 0 auto;
  padding-left: 24px;
}
.skills-content li {
  margin-bottom: 20px;
}
.footer {
  padding-top: 24px;
  padding-bottom: 16px;
}

.method {
  text-align: center;
}
a {
  color: #460a0a;
  text-decoration: none;
}
a:hover {
  text-decoration-line: underline;
}
.modify {
  display: flex;
  justify-content: center;
}
.modify a {
  margin-left: 10px;
  margin-right: 10px;
}
.fblack {
  color: red;
}
